body {
  background: rgb(240, 240, 240);
}
* {
  outline: none;
}
.app-container {
  width:900px;
  height:500px;
  margin: auto;
  margin-top:60px;
  background: #fff;
  padding:40px;
  -webkit-box-shadow: 0px 13px 26px 0px rgba(0,0,0,0.35);
          box-shadow: 0px 13px 26px 0px rgba(0,0,0,0.35);
}
a {
  text-decoration: none;
}
ul{
  list-style: none;
}
.flex-column-center {
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.forecast-header {
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between
}

.home-container {
  height:80%;
  width:100%;

}
.header {
  text-align: center;
}
.homeArrow {
  font-size: 24px;
  /* transform: rotate(180deg); */
}
.dayContainer {
  display: -ms-flexbox;
  display: flex;
}

.dayWeatherInfo {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  margin:auto;
}

.weatherIcon {
  width:300px;
}

.dayItemSubheader {
  font-size:48px;
}

.weekDayContainer {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height:150px;
}

.weekDayItemContainer {
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width:50px;
  margin-top:auto;
  margin-bottom:auto;
}

.descriptionContainer {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align:center;
      align-items:center;
  margin:auto;
}

.detailsContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.button {
  position:realative;
  width: 210px;
  height:35px;
  font-size: 20px;
  text-align: center;
  /* background: #16c26e !important; */
  background-image: -webkit-linear-gradient( 315deg, #ABDCFF 1000%, #0396FF 10%);
  background-image: -o-linear-gradient( 315deg, #ABDCFF 1000%, #0396FF 10%);
  background-image: linear-gradient( 135deg, #ABDCFF 1000%, #0396FF 10%);
  border-radius: 0;
  border:none;
  cursor: pointer;
}
input {
  width: 210px;
  height: 35px;
  font-size: 20px;
}

.fa-crosshairs {
  color: #000;
  font-size:30px;
}
.noClick{
  pointer-events: none;
}
.loader {
    border: 4px solid #f3f3f3; /* Light grey */
    border-top: 4px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 16px;
    height: 16px;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


